import React from 'react';
import {getAll} from './api/tickets';
import './App.css';


class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      tickets: getAll(),
    }
  }

  render () {
    return (
      <div className="App">
        <div class="container-fluid">
          <div class="row">
            <div class="col-md-2">
              <section>
                <p>
                  <strong>ВАЛЮТА:</strong>
                  <form action="/action_page.php" method="get">
                    <input type="button" name="transplant" value="RUB" class="btn btn-link"/>
                    <input type="button" name="transplant" value="USD" class="btn btn-link"/>
                    <input type="button" name="transplant" value="EUR" class="btn btn-link"/> 
                  </form>
                </p>
                <p>
                  <strong>КОЛИЧЕСТВО ПЕРЕСАДОК:</strong>
                  <form action="/action_page.php" method="get" class="filter">
                    <input type="checkbox" name="transplant" value="All"/> Все  <br/>
                    <input type="checkbox" name="transplant" value="Nall"/> Без пересадок <br/>
                    <input type="checkbox" name="transplant" value="One"/> 1 пересадка<br/>
                    <input type="checkbox" name="transplant" value="Two"/> 2 пересадки<br/>
                    <input type="checkbox" name="transplant" value="Three" /> 3 пересадки<br/>
                  </form>
                </p>
              </section>
              <section>
                <p>КОРЗИНА</p>
                <ul>
                  <li>Билет 1</li>
                  <li>Билет 2</li>
                </ul>
              </section>
            </div>
      
          
            <div class="col-md-10">
              <ul class="tickets">
                {this.state.tickets.map(ticket => (
                  <li class="thumbnail">
                    <div class="tickets__btn-buy-wrapper">
                      <p>TURKISH<br/> AIRLINES</p>
                      <img alt="Logo" src="img/logo.png"/>
                      <button class="btn btn-success">
                        Купить<br/>за {ticket.price} 
                      </button>
                    </div>
                    <div class="tickets__info-wrapper">
                      <div>
                        <p class="time">{ticket.departure_time}</p>
                        <p class="info-transplan">{ticket.stops} ПЕРЕСАДКА</p>
                        <p class="glyphicon glyphicon-plane">
                        </p>
                        <p class="time">{ticket.arrival_time}</p>
                      </div>
                      <div>
                        <div>
                          <p><strong>{ticket.origin}, {ticket.origin_name}</strong></p>
                          <p class="data">{ticket.departure_date}</p>
                        </div>
                        <div>
                          <p><strong>{ticket.destination_name}, {ticket.destination}</strong></p>
                          <p class="data">{ticket.arrival_date}</p> 
                        </div>                       
                      </div>
                    </div>
                  </li>
                ))} 
              </ul>
      
            </div>
          </div>
        </div>
      </div>
    );
  }
} 

export default App;
